p.error {
  color: red;
  display: none;
}

select.breed-select {
  display: block;
}

.loader {
  width: 480px;
  height: 480px;
  box-sizing: border-box;
  border: 5px dotted #fff;
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: none;
  position: relative;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

